import { Directive, ElementRef, Input, OnInit, Renderer2 } from '@angular/core';
import { DataTableBodyComponent } from './datatable-body.component';

@Directive({ selector: '[cell-styler]' })
export class CellStylerDirective implements OnInit {

    @Input('cell-styler') options = null;

    constructor(private dtb: DataTableBodyComponent, private elRef: ElementRef, private render: Renderer2) {
    }

    ngOnInit(): void {
        if (this.dtb && this.options) {
            const {val, row, col, index} = this.options;
            let _sty = null;
            if (!col) {
                _sty = this.dtb.renderRowStyle(row, index) || {};
            } else {
                let rowStyle = this.dtb.renderRowStyle(row, index) || {};
                let cellStyle = this.dtb.renderCellStyle(val, row,index, col) || {};
                _sty = cellStyle;
                if (col.fixed === 'right') {
                    const hasBackground = Object.keys(cellStyle).find(n => {
                        return ['background', 'background-color', 'backgroundcolor'].indexOf(n.toLowerCase()) > -1;
                    });
                    if (hasBackground !== null) {
                        _sty = Object.assign(cellStyle, rowStyle);
                    }
                } 
            }

            if (_sty) {
                Object.keys(_sty).forEach(n => {
                    this.elRef.nativeElement.style.setProperty(n, _sty[n], 'important');
                });
            }
        }
    }
}